
/* navbarのCSS */
.navigation {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    background-color: #2994B2;
    color: white;
    box-shadow: 0 1px 1px 1px rgba(9, 9, 9, 0.23);
}


.brand-name {
    text-decoration: none;
    color: black;
    font-size: 1.3rem;
    margin-left: 1rem;
}


.navigation-menu ul {
    display: flex;
    padding: 0;
}

.navigation-menu li {
    list-style-type: none;
    margin: 0 1rem;
}

.navigation-menu li a {
    text-decoration: none;
    display: block;
    width: 100%;
}



/* task listにあてるCSS */
.navigation-tasks {
    margin-left: auto;
    width: 30%;
    height: 100%;
    text-align: center;
}

.navigation-tasks .navogation-task-title {
    text-decoration: none;
    height: 60px;
    line-height: 60px;
}

.nav-task-list {
    list-style-type: none;
}

ul.dropdown-lists {
    padding: 0;    
}

.dropdown-lists {
    display: none;/*デフォルトでは非表示の状態にしておく*/
    width: inherit;
    position: absolute;
    top: 60px;
    z-index: 1;
}


.navigation-tasks:hover .dropdown-lists {
    display: block;/*Gナビメニューにホバーしたら表示*/
}
.navigation-tasks:hover {
    background-color: #24819B;
}

.dropdown-list {
    background-color: #004d80;
    height: 60px;
    transition: all .3s;
    position: relative;
    list-style-type: none;
}
.dropdown-list::before{
    content: "";
    width: 100%;
    height: 1px;
    background-color: #3492d1;
    position: absolute;
    top: 0;
    left: 0;
}
.dropdown-list:hover {
    background-color: #003558;
}
.dropdown-list a {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-decoration: none;
    position: relative;
}
.dropdown-list a::before {
    content: '';
    display: block;
    width: 6px;
    height: 6px;
    border-top: 2px solid #fff;
    border-left: 2px solid #fff;
    transform: rotate(135deg);
    position: absolute;
    right: 15px;
    top: calc(50% - 5px);
}